import * as React from 'react';
import { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from '@mui/material/Grid';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Autocomplete from '@mui/material/Autocomplete';
import optionDevices from '../data/optionsDevices';

export default function FormMidiCommand({ footAction, onChangeFootAction, selectedFootData }) {
  const handleChange = (key, value) => {
    if (key === "footAction" && value === "noAction") {
      onChangeFootAction({
        ...footAction,
        footAction: value,
        midiChannel: 1,
        midiCommandTypeName: "",
        midiParameter1: 0,
        midiParameter2: 0
      });
    } else {
      onChangeFootAction({
        ...footAction,
        [key]: value
      });
    }
  };

  return (
    <React.Fragment>
      <Grid container spacing={2}>
        {/* First row: Foot Action and MIDI Channel */}
        <Grid item xs={12} md={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="foot-action-label">Foot Action</InputLabel>
            <Select
              labelId="foot-action-label"
              value={footAction.footAction}
              onChange={(event) => handleChange("footAction", event.target.value)}
            >
              <MenuItem value={"noAction"}>No Action</MenuItem>
              {selectedFootData?.behavior === 'press' ? [
                <MenuItem key="press" value={"press"}>Press</MenuItem>,
                <MenuItem key="longPress" value={"longPress"}>Long Press</MenuItem>
              ] : selectedFootData?.behavior === 'stomp' ? [
                <MenuItem key="activated" value={"activated"}>Activated</MenuItem>,
                <MenuItem key="deactivated" value={"deactivated"}>Deactivated</MenuItem>
              ] : null}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} md={6}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="midi-channel-label">Midi Channel</InputLabel>
            <Select
              labelId="midi-channel-label"
              value={footAction.midiChannel}
              onChange={(event) => handleChange("midiChannel", event.target.value)}
              disabled={footAction.footAction === "noAction"}
            >
              {[...Array(16)].map((_, i) => (
                <MenuItem key={i + 1} value={i + 1}>Channel {i + 1}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>

        {/* Second row: MIDI Command */}
        <Grid item xs={12}>
          <FormControl variant="filled" fullWidth>
            <InputLabel id="midi-command-label">Midi Command</InputLabel>
            <Select
              labelId="midi-command-label"
              value={footAction.midiCommandTypeName}
              onChange={(event) => handleChange("midiCommandTypeName", event.target.value)}
              disabled={footAction.footAction === "noAction"}
            >
              <MenuItem value={"ProgramChange"}>Program Change</MenuItem>
              <MenuItem value={"ControlChange"}>Control Change</MenuItem>
              {/* <MenuItem value={"SystemExclusive"}>System Exclusive</MenuItem> */}
            </Select>
          </FormControl>
        </Grid>

        {/* Third row: MIDI Parameters */}
        {footAction.midiCommandTypeName === "SystemExclusive" ? (
          <Grid item xs={12}>
            <TextField
              fullWidth
              margin="dense"
              id="sysExData"
              name="sysExData"
              label="SysEx Data (hex format, space separated)"
              type="text"
              variant="filled"
              value={footAction.midiParameter1 || ''}
              onChange={(event) => {
                const hexValue = event.target.value.trim();
                // Validate hex format
                if (/^[0-9A-Fa-f\s]*$/.test(hexValue)) {
                  handleChange("midiParameter1", hexValue);
                }
              }}
              disabled={footAction.footAction === "noAction"}
              helperText="Example: F0 00 01 02 F7"
            />
          </Grid>
        ) : (
          <>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                margin="dense"
                id="midiData01"
                name="midiData01"
                label="Midi Data 01"
                type="number"
                variant="filled"
                value={footAction.midiParameter1}
                onChange={(event) => handleChange("midiParameter1", parseInt(event.target.value))}
                disabled={footAction.footAction === "noAction"}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                margin="dense"
                id="midiData02"
                name="midiData02"
                label="Midi Data 02"
                type="number"
                variant="filled"
                value={footAction.midiParameter2}
                onChange={(event) => handleChange("midiParameter2", parseInt(event.target.value))}
                disabled={footAction.footAction === "noAction" || footAction.midiCommandTypeName === "ProgramChange"}
              />
            </Grid>
          </>
        )}
      </Grid>
    </React.Fragment>
  );
}
