import { useState, useEffect } from 'react';
import './App.css';
import Typography from '@mui/material/Typography';
import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import MidiLiveMode from './components/MidiLiveMode';
import MidiDataMenu from './components/MidiDataMenu';
import SerialConnection from './components/SerialConnection';
import NavigationMenu from './components/NavigationMenu';

import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

import logo from './logo.png';

import MidiSVG from './devices/MidiSVG';
import defaultMidiData from './data/defaultMidiData.json';
import FormDialog from './components/FormDialog';
import Button from '@mui/material/Button';
import MidiDataTable from './components/MidiDataTable';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
});

function App() {

  const [selectedBank, setSelectedBank] = useState(1);
  const [selectedFootIndex, setSelectedFootIndex] = useState(1);
  const [midiData, setMidiData] = useState(defaultMidiData);
  const [formDialogOpen, setFormDialogOpen] = useState(false);
  const [isLiveMode, setIsLiveMode] = useState(false);
  const [midiTableOpen, setMidiTableOpen] = useState(false);
  const [variableDialogOpen, setVariableDialogOpen] = useState(false);
  const [led1State, setLed1State] = useState(false);
  const [led2State, setLed2State] = useState(false);
  const [led3State, setLed3State] = useState(false);
  const [led4State, setLed4State] = useState(false);
  const [led5State, setLed5State] = useState(false);
  const [led6State, setLed6State] = useState(false);
  const [led7State, setLed7State] = useState(false);
  const [led8State, setLed8State] = useState(false);
  const [toggleAnimations, setToggleAnimations] = useState({});
  const [pressStartTimes, setPressStartTimes] = useState({});
  const [blinkIntervals, setBlinkIntervals] = useState({});
  const LONG_PRESS_DURATION = 700; // time in milliseconds
  const BLINK_INTERVAL = 700; // time in milliseconds

  const formatMidiDataAsVariables = () => {
    return midiData.map(foot => {
      const variables = [];
      // Convert hex color to RGB components
      const hexToRgb = (hex) => {
        const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        return result ? {
          r: parseInt(result[1], 16),
          g: parseInt(result[2], 16),
          b: parseInt(result[3], 16)
        } : { r: 0, g: 0, b: 0 };
      };
      const rgbColor = hexToRgb(foot.ledColor);
      
      // Add behavior and split LED color into RGB components
      variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_NAME=${foot.name}`);
      variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_BEHAVIOR=${foot.behavior}`);
      variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_LED_COLOR_R=${rgbColor.r}`);
      variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_LED_COLOR_G=${rgbColor.g}`);
      variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_LED_COLOR_B=${rgbColor.b}`);
      
      foot.footActions.forEach((action, index) => {
        variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_COMMAND_${index+1}_FOOT_ACTION=${action.footAction}`);
        variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_COMMAND_${index+1}_MIDI_CHANNEL=${action.midiChannel}`);
        variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_COMMAND_${index+1}_MIDI_COMMAND_TYPE_NAME=${action.midiCommandTypeName}`);
        variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_COMMAND_${index+1}_MIDI_PARAMETER_1=${action.midiParameter1}`);
        variables.push(`BANK_${foot.bank}_FOOT_${foot.footIndex}_COMMAND_${index+1}_MIDI_PARAMETER_2=${action.midiParameter2}`);
      });
      return variables.join('\n');
    }).join('\n');
  };

  const [footData, setFootData] = useState(defaultMidiData[0]);

  const handleChangeSelectedBank = (event) => {
    setSelectedBank(event.target.value);
  };

  const stopAllBlinkingLEDs = () => {
    Object.keys(blinkIntervals).forEach(key => {
      clearInterval(blinkIntervals[key]);
    });
    setBlinkIntervals({});
  };

  const alternateToggleState = (footKey) => {
    const footIndex = parseInt(footKey.split('-')[1]);
    let currentState, setterFunction;
    
    switch(footIndex) {
      case 1: [currentState, setterFunction] = [led1State, setLed1State]; break;
      case 2: [currentState, setterFunction] = [led2State, setLed2State]; break;
      case 3: [currentState, setterFunction] = [led3State, setLed3State]; break;
      case 4: [currentState, setterFunction] = [led4State, setLed4State]; break;
      case 5: [currentState, setterFunction] = [led5State, setLed5State]; break;
      case 6: [currentState, setterFunction] = [led6State, setLed6State]; break;
      case 7: [currentState, setterFunction] = [led7State, setLed7State]; break;
      case 8: [currentState, setterFunction] = [led8State, setLed8State]; break;
      default: return false;
    }
    
    const newState = !currentState;
    setterFunction(newState);
    return newState;
  };

  const updateFootState = (footData, footKey, pressMode) => {
    
    // ----------------------
    // BEHAVIOR: STOMP
    if (footData.behavior === 'stomp') {
      console.log(footKey, 'BEHAVIOR: STOMP');
      const newState = alternateToggleState(footKey);
      if (isLiveMode) {
        sendMidiData(footData.footIndex, newState ? 'activated' : 'deactivated');
      }
    }

    // ----------------------
    // BEHAVIOR: PRESS / PRESS MODE: PRESS
    else if (footData.behavior === 'press' && pressMode === 'press') {
      console.log(footKey, 'BEHAVIOR: PRESS / PRESS MODE: PRESS');

      // Activate only pressed led
      stopAllBlinkingLEDs();
      const footIndex = parseInt(footKey.split('-')[1]);
      midiData.filter((item) => item.bank == selectedBank && item.behavior === 'press').forEach((item) => {
        switch(item.footIndex) {
          case 1: setLed1State(item.footIndex == footIndex ? true : false); break;
          case 2: setLed2State(item.footIndex == footIndex ? true : false); break;
          case 3: setLed3State(item.footIndex == footIndex ? true : false); break;
          case 4: setLed4State(item.footIndex == footIndex ? true : false); break;
          case 5: setLed5State(item.footIndex == footIndex ? true : false); break;
          case 6: setLed6State(item.footIndex == footIndex ? true : false); break;
          case 7: setLed7State(item.footIndex == footIndex ? true : false); break;
          case 8: setLed8State(item.footIndex == footIndex ? true : false); break;
        }
      });

      // Send midi data
      if (isLiveMode) {
        sendMidiData(footData.footIndex, 'press');
      }
    }

    // ----------------------
    // BEHAVIOR: PRESS / PRESS MODE: LONG PRESS
    else if (footData.behavior === 'press' && pressMode === 'longPress') {
      console.log(footKey, 'BEHAVIOR: PRESS / PRESS MODE: LONG PRESS');

      // Activate only pressed led
      stopAllBlinkingLEDs();
      midiData.filter((item) => item.bank == selectedBank && item.behavior === 'press').forEach((item) => {
        switch(item.footIndex) {
          case 1: setLed1State(false); break;
          case 2: setLed2State(false); break;
          case 3: setLed3State(false); break;
          case 4: setLed4State(false); break;
          case 5: setLed5State(false); break;
          case 6: setLed6State(false); break;
          case 7: setLed7State(false); break;
          case 8: setLed8State(false); break;
        }
      });

      // Start blinking for current led key
      let currentState = false;
      const interval = setInterval(() => {
        const footIndex = parseInt(footKey.split('-')[1]);
        switch(footIndex) {
          case 1: setLed1State(!currentState); break;
          case 2: setLed2State(!currentState); break;
          case 3: setLed3State(!currentState); break;
          case 4: setLed4State(!currentState); break;
          case 5: setLed5State(!currentState); break;
          case 6: setLed6State(!currentState); break;
          case 7: setLed7State(!currentState); break;
          case 8: setLed8State(!currentState); break;
        }
        currentState = !currentState;
      }, BLINK_INTERVAL);
      setBlinkIntervals(prev => ({ ...prev, [footKey]: interval }));

      // Send midi data
      if (isLiveMode) {
        sendMidiData(footData.footIndex, 'longPress');
      }
    }
  };

  const sendMidiData = (footIndex, actionType = null) => {
    window.dispatchEvent(new CustomEvent('midiFootClick', { 
      detail: { 
        footIndex: parseInt(footIndex),
        actionType: actionType
      } 
    }));
  };

  const handleOnClickFoot = (event) => {

    const footIndex = event.target.id.split('-')[1];
    const footData = midiData.find((item) => item.bank == selectedBank && item.footIndex == footIndex);
    const key = `${selectedBank}-${footIndex}`;

    setSelectedFootIndex(footIndex);
    setFootData(footData);

    if (event.type === 'mousedown') {
      // Start tracking press time for potential long press
      setPressStartTimes(prev => ({ ...prev, [key]: Date.now() }));
    } else if (event.type === 'mouseup') {
      const pressStartTime = pressStartTimes[key];
      const pressDuration = Date.now() - pressStartTime;
      
      // Clear the press start time
      setPressStartTimes(prev => {
        const newTimes = { ...prev };
        delete newTimes[key];
        return newTimes;
      });

      if (pressDuration >= LONG_PRESS_DURATION) {
        const pressMode = 'longPress';
        updateFootState(footData, key, pressMode);
      } else {
        const pressMode = 'press';
        updateFootState(footData, key, pressMode);
      }

    }

    if (!isLiveMode) {
      setFormDialogOpen(true);
    }
  };

  const handleOnCloseFormDialog = (event) => {
    setFormDialogOpen(false);
  };
  const handleOnChangeFootData = (key, value) => {
    setFootData(prevState => ({
      ...prevState,
      [key]: value
    }));
  };
  const handleOnSaveFormDialog = () => {
    const updatedMidiData = [...midiData];
    const dataIndex = midiData.findIndex((item) => item.bank == selectedBank && item.footIndex == selectedFootIndex);
    if (dataIndex !== -1) {
      updatedMidiData[dataIndex] = footData;
      setMidiData(updatedMidiData);
    }
    setFormDialogOpen(false);
  };

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <div className="app" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '100vh' }}>
        <div className="app-content" style={{  overflow: 'auto', padding: '20px', width: '100%' }}>
          
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
            {/* <img src={logo} alt="Polvo Logo" style={{ height: '80px', width: '80px' }} /> */}
            <Typography variant="h2" gutterBottom sx={{ fontSize: { xs: '2rem', sm: '3rem' }, mb: 0 }}>
              Midi Controller
            </Typography>
          </Box>
          <NavigationMenu>
            <MidiLiveMode midiData={midiData} selectedBank={selectedBank} onLiveModeChange={setIsLiveMode} />
            <SerialConnection midiData={midiData} />
          </NavigationMenu>
          {/* --------------- SELEÇÃO DE BANCO --------------- */}
          <FormControl variant="filled" sx={{ m: 1, width: { xs: '100%', sm: 300 }, maxWidth: '100%', paddingBottom: 3, display: 'none' }}>
            <InputLabel id="demo-simple-select-label">Bank</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedBank}
              label="Age"
              onChange={handleChangeSelectedBank}
            >
              <MenuItem value={1}>Bank 1</MenuItem>
              <MenuItem value={2}>Bank 2</MenuItem>
              <MenuItem value={3}>Bank 3</MenuItem>
              <MenuItem value={4}>Bank 4</MenuItem>
              <MenuItem value={5}>Bank 5</MenuItem>
              <MenuItem value={6}>Bank 6</MenuItem>
              <MenuItem value={7}>Bank 7</MenuItem>
              <MenuItem value={8}>Bank 8</MenuItem>
            </Select>
          </FormControl>
          {/* --------------- CONTROLADOR MIDI --------------- */}
          <Box sx={{ width: '100%', maxWidth: '800px', margin: '0 auto' }}>
            <MidiSVG
              footName1={midiData.find((item) => item.bank == selectedBank && item.footIndex == 1).name}
              footName2={midiData.find((item) => item.bank == selectedBank && item.footIndex == 2).name}
              footName3={midiData.find((item) => item.bank == selectedBank && item.footIndex == 3).name}
              footName4={midiData.find((item) => item.bank == selectedBank && item.footIndex == 4).name}
              footName5={midiData.find((item) => item.bank == selectedBank && item.footIndex == 5).name}
              footName6={midiData.find((item) => item.bank == selectedBank && item.footIndex == 6).name}
              footName7={midiData.find((item) => item.bank == selectedBank && item.footIndex == 7).name}
              footName8={midiData.find((item) => item.bank == selectedBank && item.footIndex == 8).name}
              ledColor1={midiData.find((item) => item.bank == selectedBank && item.footIndex == 1).ledColor}
              ledColor2={midiData.find((item) => item.bank == selectedBank && item.footIndex == 2).ledColor}
              ledColor3={midiData.find((item) => item.bank == selectedBank && item.footIndex == 3).ledColor}
              ledColor4={midiData.find((item) => item.bank == selectedBank && item.footIndex == 4).ledColor}
              ledColor5={midiData.find((item) => item.bank == selectedBank && item.footIndex == 5).ledColor}
              ledColor6={midiData.find((item) => item.bank == selectedBank && item.footIndex == 6).ledColor}
              ledColor7={midiData.find((item) => item.bank == selectedBank && item.footIndex == 7).ledColor}
              ledColor8={midiData.find((item) => item.bank == selectedBank && item.footIndex == 8).ledColor}
              handleOnClickFoot={handleOnClickFoot}
              isLiveMode={isLiveMode}
              led1State={led1State}
            led2State={led2State}
            led3State={led3State}
            led4State={led4State}
            led5State={led5State}
            led6State={led6State}
            led7State={led7State}
            led8State={led8State}
            />
          </Box>
          {/* ------------ EDIÇÃO ------------------- */}
          <MidiDataMenu
              onReset={() => setMidiData(defaultMidiData)}
              onSave={() => localStorage.setItem('midiData', JSON.stringify(midiData))}
              onExport={() => {
                const dataStr = JSON.stringify(midiData, null, 2);
                const blob = new Blob([dataStr], { type: 'application/json' });
                const url = URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = 'midi_data.json';
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
                URL.revokeObjectURL(url);
              }}
              onLoad={() => {
                const input = document.createElement('input');
                input.type = 'file';
                input.accept = '.json';
                input.onchange = (e) => {
                  const file = e.target.files[0];
                  const reader = new FileReader();
                  reader.onload = (e) => {
                    try {
                      const data = JSON.parse(e.target.result);
                      setMidiData(data);
                    } catch (error) {
                      console.error('Error parsing JSON:', error);
                    }
                  };
                  reader.readAsText(file);
                };
                input.click();
              }}
              onViewTable={() => setMidiTableOpen(true)}
              onViewVariables={() => setVariableDialogOpen(true)}
            />
          <Dialog
            open={variableDialogOpen}
            onClose={() => setVariableDialogOpen(false)}
            maxWidth="md"
            fullWidth
          >
            <DialogTitle>Config Variables</DialogTitle>
            <DialogContent>
              <pre style={{ whiteSpace: 'pre-wrap', wordBreak: 'break-all' }}>
                {formatMidiDataAsVariables()}
              </pre>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setVariableDialogOpen(false)}>Close</Button>
            </DialogActions>
          </Dialog>
          <FormDialog
            open={formDialogOpen}
            onCloseFormDialog={handleOnCloseFormDialog}
            onSaveFormDialog={handleOnSaveFormDialog}
            onChangeFootData={handleOnChangeFootData}
            selectedBank={selectedBank}
            selectedFootIndex={selectedFootIndex}
            selectedFootData={footData}
          />
          <MidiDataTable
            open={midiTableOpen}
            onClose={() => setMidiTableOpen(false)}
            midiData={midiData}
          />
        </div>
      </div>
    </ThemeProvider>
  );
}

export default App;