import * as React from 'react';
import { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import FormMidiCommand from './FormMidiCommand';

import Accordion from '@mui/material/Accordion';
import AccordionActions from '@mui/material/AccordionActions';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Divider, Box, Grid } from '@mui/material';
import { PREDEFINED_COLORS } from '../constants/colors';

export default function FormDialog({
  open,
  onCloseFormDialog,
  onSaveFormDialog,
  onChangeFootData,
  selectedBank,
  selectedFootIndex,
  selectedFootData,
}) {

  return (
    <React.Fragment>
      <Dialog
        onClose={onCloseFormDialog}
        aria-labelledby="customized-dialog-title"
        open={open}
        fullWidth={true}
      >
        <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
          Bank {selectedBank} / Foot {selectedFootIndex}
        </DialogTitle>
        <IconButton
          aria-label="close"
          onClick={onCloseFormDialog}
          sx={(theme) => ({
            position: 'absolute',
            right: 8,
            top: 8,
            color: theme.palette.grey[500],
          })}
        >
          <CloseIcon />
        </IconButton>


          {/* ------- NAME AND BEHAVIOR ----------- */}
        <DialogContent>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <TextField
                autoFocus
                required
                margin="dense"
                id="name"
                name="name"
                label="Name"
                type="text"
                value={selectedFootData.name}
                fullWidth
                variant="standard"
                onChange={(event) => onChangeFootData("name", event.target.value)}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth variant="standard" sx={{ mt: 1 }}>
                <InputLabel>Behavior</InputLabel>
                <Select
                  value={selectedFootData.behavior || "press"}
                  onChange={(event) => onChangeFootData("behavior", event.target.value)}
                >
                  <MenuItem value="press">Press</MenuItem>
                  <MenuItem value="stomp">Stomp</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
          
          {/* ------- LED COLOR ----------- */}
          <Divider sx={{ paddingTop:2 }} />
          <Box sx={{ mt: 2 }}>
            <Typography variant="subtitle1" gutterBottom>LED Color</Typography>
            <Grid container spacing={1}>
              {PREDEFINED_COLORS.map((color, index) => (
                <Grid item key={index}>
                  <Button
                    sx={{
                      minWidth: '40px',
                      height: '40px',
                      backgroundColor: color,
                      border: selectedFootData.ledColor === color ? '3px solid white' : '1px solid grey',
                      '&:hover': {
                        backgroundColor: color,
                        opacity: 0.8
                      }
                    }}
                    onClick={() => onChangeFootData("ledColor", color)}
                  />
                </Grid>
              ))}
            </Grid>
          </Box>
        </DialogContent>

        <DialogContent dividers>
          {selectedFootData.footActions.map((footAction, key) => (
            <Accordion key={key}>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls={`panel${key}-content`}
                id={`panel${key}-header`}
              >
                <Typography component="span">Command {key + 1}</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <FormMidiCommand
                  footAction={footAction}
                  selectedFootData={selectedFootData}
                  onChangeFootAction={(updatedFootAction) => {
                    const updatedFootActions = [...selectedFootData.footActions];
                    updatedFootActions[key] = updatedFootAction;
                    onChangeFootData("footActions", updatedFootActions);
                  }}
                />
              </AccordionDetails>
            </Accordion>
          ))}

        </DialogContent>

        <DialogActions>
          <Button onClick={onCloseFormDialog}>Cancel</Button>
          <Button onClick={() => onSaveFormDialog(selectedBank, selectedFootIndex, selectedFootData)} variant='outlined' type="submit">Save</Button>
        </DialogActions>
      </Dialog>


    </React.Fragment>
  );
}
