// SerialCommunication.js

class SerialCommunication {
  constructor() {
    this.port = null;
    this.reader = null;
    this.writer = null;
    this.isConnected = false;
    this.encoder = new TextEncoder();
    this.decoder = new TextDecoder();
    this.messageBuffer = '';
  }

  async connect() {
    try {
      console.log('Initializing serial connection...');
      
      // Request port access
      this.port = await navigator.serial.requestPort();
      await this.port.open({ baudRate: 9600 }); // Match Arduino baud rate
      
      // Create reader and writer
      this.reader = this.port.readable.getReader();
      this.writer = this.port.writable.getWriter();
      
      // Start reading loop
      this.startReading();
      
      this.isConnected = true;
      console.log('✅ Serial connection successfully established');
      return true;
    } catch (error) {
      console.error('❌ Failed to establish serial connection:', error.message);
      return false;
    }
  }

  async disconnect() {
    try {
      if (this.reader) {
        await this.reader.cancel();
        await this.reader.releaseLock();
      }
      if (this.writer) {
        await this.writer.releaseLock();
      }
      if (this.port) {
        await this.port.close();
      }
      this.isConnected = false;
      console.log('✅ Serial connection successfully closed');
    } catch (error) {
      console.error('❌ Error during serial port disconnection:', error.message);
    }
  }

  async startReading() {
    while (true) {
      try {
        const { value, done } = await this.reader.read();
        if (done) {
          break;
        }
        
        // Append new data to buffer
        this.messageBuffer += this.decoder.decode(value);
        
        // Process complete messages
        while (this.messageBuffer.includes('\n')) {
          const newlineIndex = this.messageBuffer.indexOf('\n');
          const message = this.messageBuffer.slice(0, newlineIndex);
          this.messageBuffer = this.messageBuffer.slice(newlineIndex + 1);
          
          if (message.trim()) {
            try {
              const data = JSON.parse(message);
              console.log('📥 Received data from Midi Controller:', data);
            } catch (e) {
              console.log('📥 Received raw data from Midi Controller:', message);
            }
          }
        }
      } catch (error) {
        console.error('Error reading data:', error);
        break;
      }
    }
  }

  async sendMidiData(midiData) {
    if (!this.writer || !this.isConnected) {
      console.error('❌ Cannot send MIDI data: No serial connection available');
      return false;
    }

    try {
      console.log('📤 Sending MIDI data to Midi Controller...');
      // const formattedData = this.formatMidiDataForArduino(midiData);
      // // Send each foot configuration separately
      // for (const footConfig of formattedData) {
      //   const data = JSON.stringify(footConfig) + '\n';
      //   await this.writer.write(this.encoder.encode(data));
      // }

      await this.writer.write(this.encoder.encode("Teste"));
      
      console.log('✅ MIDI data successfully sent to Midi Controller');
      return true;
    } catch (error) {
      console.error('❌ Failed to send MIDI data:', error.message);
      return false;
    }
  }

  formatMidiDataForArduino(midiData) {
    return midiData.map(foot => ({
      type: 'midi-config',
      footIndex: foot.footIndex,
      ledColor: foot.ledColor ? {
        r: Math.min(255, Math.max(0, parseInt(foot.ledColor.r) || 0)),
        g: Math.min(255, Math.max(0, parseInt(foot.ledColor.g) || 0)),
        b: Math.min(255, Math.max(0, parseInt(foot.ledColor.b) || 0))
      } : null,
      actions: foot.footActions.map(action => ({
        type: action.footAction,
        midiChannel: action.midiChannel,
        parameter1: action.midiParameter1,
        parameter2: action.midiParameter2
      }))
    }));
  }
}

export default new SerialCommunication();