import React, { useState } from 'react';
import { Button, Box, Alert, Tooltip, Switch } from '@mui/material';
import SerialCommunication from '../utils/SerialCommunication';
import SaveIcon from '@mui/icons-material/Save';

export default function SerialConnection({ midiData, onConnectionChange }) {
  const [isConnected, setIsConnected] = useState(false);
  const [error, setError] = useState(null);

  const handleConnect = async () => {
    console.log('Initializing serial connection...');
    try {
      const connected = await SerialCommunication.connect();
      setIsConnected(connected);
      setError(null);
      if (onConnectionChange) {
        onConnectionChange(connected);
      }
    } catch (err) {
      setError('Failed to connect to Midi Controller: ' + err.message);
      setIsConnected(false);
    }
  };

  const handleDisconnect = async () => {
    try {
      await SerialCommunication.disconnect();
      setIsConnected(false);
      setError(null);
      if (onConnectionChange) {
        onConnectionChange(false);
      }
    } catch (err) {
      setError('Error disconnecting from Midi Controller: ' + err.message);
    }
  };

  const handleSendToArduino = async () => {
    try {
      const success = await SerialCommunication.sendMidiData(midiData);
      if (!success) {
        setError('Failed to send data to Midi Controller');
      }
    } catch (err) {
      setError('Error sending data to Midi Controller: ' + err.message);
    }
  };

  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
      {/* <Tooltip title="Midi controller device connection">
        <Button
          variant="text"
          color={isConnected ? 'success' : 'inherit'}
          onClick={isConnected ? handleDisconnect : handleConnect}
          startIcon={<Switch checked={isConnected} size="small" />}
          sx={{ color: isConnected ? undefined : '#666' }}
        >
          {isConnected ? 'Connected' : 'Connect to Device'}
        </Button>
      </Tooltip> */}

      {isConnected && (
        <Tooltip title="Save midi commands configuration do Midi Controller device.">
          <Button
            variant="text"
            color="inherit"
            startIcon={<SaveIcon />}
            onClick={handleSendToArduino}
            sx={{ color: 'white' }}
          >
            Save Config
          </Button>
        </Tooltip>
      )}

      {error && (
        <Alert severity="error" sx={{ py: 0, px: 1 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
}